import lrz from 'lrz';
import uploaditem from './uploaditem.vue';
export default{
  props: [
    'fieldname',
    'typefieldid',
    'max',
    'items',
  ],
    data(){
      return{
        add_show:true
      }
    },
    components:{
      uploaditem
    },
    computed:{
      total(){
        return this.items.length
      }
    },
    mounted(){
      let self = this;
      document.querySelector('#'+this.typefieldid).addEventListener('change', function () {
        if (this.files.length === 0) return;    //解决PC端点击取消的报错现象
        lrz(this.files[0])
          .then(function (rst) {
            console.log(rst);
            // 处理成功会执行
            self.items.push({'val':self.fieldname,'desc':rst.base64});
            console.log(self.items.length);
            if(self.items.length>=self.max){
              self.add_show=false;
            }
          })
          .catch(function (err) {
            // 处理失败会执行
            console.log(err);
          })
          .always(function () {
            // 不管是成功失败，都会执行
          });
      });
    },
    methods:{
      delItem(index){
          console.log(index);
      }
    }
}



